import React, { PropTypes, Component } from 'react'
import classNames from 'classnames';
import { Link } from 'react-router'
export default class List extends Component {
    render() {
        return (
            <div className="weui_panel_bd">
                {this.props.list.map((item, i) =>
                    <Link key={i} to={"/article?id="+i} className={classNames("weui_media_box","weui_media_appmsg")}>
                        <div className="weui_media_hd">
                            <img className="weui_media_appmsg_thumb" src="data:image/png;base64," />
                        </div>
                        <div className="weui_media_bd">
                            <h4 className="weui_media_title">标题一</h4>
                            <p className="weui_media_desc">由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。</p>
                        </div>
                    </Link>
                )}
            </div>
        )
    }
}

List.propTypes = {
    list: PropTypes.array.isRequired
}


